@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: SuperWebcomicBros;
  overflow-x: hidden;
  overflow-y: scroll;
}

.image-swiper-button-next {
  width: 50px;
  height: 50px;
  background-color: red;
  border-radius: 100%;
}

.slider-timeline-track-0 {
  @apply h-1 bg-blue-600;
}

.slider-timeline-track-1 {
  @apply h-1 bg-red-600;
}

.slider-timeline-track-2 {
  @apply h-1 bg-white;
}

.slider-timelime-thumb-0 {
  @apply w-2 h-2 rounded-full bg-red-600;
}

.slider-timelime-thumb-1 {
  @apply hidden;
}

.pageTextureOverlay {
  transform: translate3d(0, 0, 0);
  @apply mix-blend-multiply z-[99] pointer-events-none bg-cover right-0 left-0 bottom-0 top-0 absolute;
}

.heroSection-elem {
  position: relative;
}

.heroSection {
  display: grid;
  grid-template-areas:
    "a a"
    "b c"
    "d d";
  grid-template-columns: 1fr 1fr;
}

.heroSection-a {
  height: 165px;
  grid-area: a;
}

.heroSection-b {
  height: 190px;
  grid-area: b;
}

.heroSection-c {
  height: 190px;
  grid-area: c;
}

.heroSection-d {
  height: 120px;
  grid-area: d;
}

.heroSection-d > img {
  @apply absolute bottom-0;
}

.heroSection-c > img {
  @apply absolute bottom-0 left-[4%];
}

.heroSection-b > img {
  @apply absolute;
}

.heroSection-a > img {
  @apply absolute bottom-0 right-0;
}

@media (min-width: 1280px) {
  .heroSection-elem {
    height: 255px;
  }

  .heroSection {
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
      "b a"
      "b c"
      "d d";
  }

  .heroSection-d > img {
    @apply absolute bottom-0 right-[10%];
  }

  .heroSection-c > img {
    @apply absolute bottom-0 left-[4%];
  }

  .heroSection-b > img {
    @apply absolute top-[40%] right-[28%];
  }

  .heroSection-a > img {
    @apply absolute w-[1208px] max-w-none bottom-0 -right-[15%];
  }
}

@media (min-width: 1800px) {
  .heroSection-elem {
    height: 255px;
  }

  .heroSection-d > img {
    @apply absolute bottom-0 right-[10%];
  }

  .heroSection-c > img {
    @apply absolute bottom-0 left-[4%];
  }

  .heroSection-b > img {
    @apply absolute top-[40%] -right-[13%];
  }

  .heroSection-a > img {
    @apply absolute w-[1208px] max-w-none bottom-0 right-0;
  }

  .heroSection {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-areas:
      "b a a c"
      "b d d d";
  }
}
